body {
  display: grid;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vh;
}
.container {
  background: grey;
  width: 90vh;
  height: 300px;
  display: flex;
  gap: 20px;
  overflow-x: auto;
  align-items: center;
  scroll-snap-type: x mandatory;
}
.container .child {
  scroll-snap-align: center;
}

.child {
  flex: 0 0 auto;
  width: 90%;
  height: 90%;
  background: cornflowerblue;
  border: 1px solid black;
}
.container :nth-child(1) {
  margin-left: 5vh;
}
.container :last-child {
  margin-right: 5vh;
}
.wraper {
  position: relative;
}
